.part{
	background: whitesmoke;
	//#D6EED6
}
.partBox{
	padding: 13vh 0 13vh 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	.pTitle{
		font-size: 2.5em;
		//color: #ED2647;
		transition: 1s;
		font-weight: 800;
	}
	.line{
		margin-top: 50px;
		width: 50%;
		margin-bottom: 10vh;
		text-align: center;
		line-height: 1.2;
		transition: 1s;
	}
	.compareBox{
		transition: 1s;
		display: flex;
		justify-content:center;
		width: 100%;
		z-index: 1;
		>div:first-child{
			display: flex;
			width: 1200px;
			justify-content: space-around;
			.pBox{
				box-shadow: 0 0 10px black;
				display: flex;
				border-radius: 20px;
				flex-direction: column;
				overflow: hidden;
				width: 350px;
				img{
					position: absolute;
					width: 350px;
					height: 200px;
					object-fit: cover;
					border-radius: 20px;
				}
				>div{
					width: 350px;
					height: 200px;
					background: black;
					opacity: 0.7;
					color: white;
					display: flex;
					justify-content: center;
					align-items: center;
					text-align: center;
					font-size: 1.5em;
					font-weight: 700;
					line-height: 1.5;
					span{
						color: #ED2647;
					}
				}
				ul{
					list-style: none;
					padding-left: 5px !important;
					line-height: 2.5;
					margin: 10px 0 20px 0;
					>li div{
						width: 100%;
						height: 100%;
						display: flex;
						margin-left: 10px;
						align-items: flex-start;
						img{
							width: 20px;
							height: 30px;
							margin: 0 5px 0 5px;
							object-fit: contain;
							position: static !important;
						}
					}
					
				}
			}
		}
		
		
	}
}

@media (max-width: 1100px) {
	.partBox .compareBox > div:first-child{
		flex-direction: column;
    align-items: center;
    width: 500px;
		
		.pBox{
			margin-bottom: 30px;
			width: 500px;
			img{
				width: 500px;
			}
			>div{
				width: 500px;
			}
		}
	}
}
@media (max-width: 900px) {
	
	.line {
    width: 80% !important;
	}
}
@media (max-width: 550px) {
	.partBox .compareBox > div:first-child{
    width: 300px;
		
		.pBox{
			margin-bottom: 30px;
			width: 300px;
			img{
				width: 300px;
				height: 150px;
			}
			>div{
				width: 300px;
				height: 150px;
				font-size: 1.2em;
			}
			ul{
				line-height: 2;
				font-size: 0.9em;
			}
		}
	}
	.pTitle{
		font-size: 1.8em !important;
	}
}
@media (max-width: 500px) {
	
	.partBox{
		padding: 13vh 0 5vh 0;
	}
}