.outro{
	background: white;
}
.outroBox{
	padding: 13vh 0 13vh 0;
	display: flex;
	//align-items: center;
	justify-content: space-evenly;
	transition: 1s;
	
	.ot2{
		width: 450px !important;
	}
	
	.outText{
		display: flex;
		flex-direction: column;
		width: 400px;
		justify-content: center;
		//align-items: center;
		font-weight: 800;
		.outTitle{
			font-size: 4em;
			
			line-height: 1.3;
			color: #ED2647;
		}
		.outExplain{
			font-size: 1.5em;
			margin-top: 50px;
		}
		
		
		.contact{
			font-size: 1.5em;
			padding-bottom: 10px;
			margin-bottom: 30px;
			border-bottom: solid #ED2647 2px;
			font-weight: 800;
		}
		.clist{
			display: flex;
			align-items: center;
			margin-bottom: 15px;
			font-size: 1.2em;
			cursor: pointer;
			img{
				width: 30px;
				margin-right: 15px;
			}
			span{
				color: gray;
				font-weight: 100;
			}
		}
	}
}	


@media(max-width: 1100px){
	.outroBox .outText{
		width: 300px;
		.outTitle{
			font-size: 3em;
		}
		.outExplain, .contact{
			font-size: 1.3em;
		}
		.clist{
			font-size: 1em;
		}
	} 
}

@media(max-width: 700px){
	.outroBox{
		flex-direction: column;
    align-items: center;
		text-align: center;
	}
	.outroBox .outText{
		margin-bottom: 30px;
		.outTitle{
		}
		.outExplain, .contact{
		}
		.clist{
			justify-content: center;
			//flex-direction: column;
			span{
				display: block;
			}
		}
	} 
}
@media (max-width: 500px) {
	
	.outroBox{
		padding: 13vh 0 5vh 0;
	}
	.outroBox .outText{
		.outTitle{
			font-size: 2em;
		}
		.outExplain, .contact{
			font-size: 1em;
		}
		.clist{
			justify-content: center;
			//flex-direction: column;
			span{
				display: block;
			}
		}
	} 
}