.title{
	height: 80vh;
	width: 90%;
	padding: 0 5% 0 5%;
	padding-top: 8vh;
	display: flex;
	justify-content: space-around;
	align-items: center;
	overflow: hidden;

}
.tb1{
	margin-left: 5vw;
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 500px;
	h1{
		transition: 1s;
		line-height: 1.5;
		font-size: 1.5em;
		font-weight: 700;
		span{
			font-size: 4em;
			font-weight: 800;
		}
	}
}
.st{
	//font-size: 1.1em;
	width: 500px;
	line-height: 1.5;
	transition: 1.5s;
}
.st1{
	margin-top: 50px;
}
.st2{
	margin-top: 10px;
}

.picBox{
	//border: solid;
	height: 80vh;
	overflow: hidden;
	padding: 0 30px 0 30px;
	transition: 1s;
	width: 600px;
}
.pb1, .pb2{
	height: 600px;
	width: 600px;
	object-fit: cover;
	object-position: center;
	border-radius: 30px;
	//box-shadow: 0px 0px 15px yellow;
}
.pb1{	
	transform: translate(30px, -200px) rotate(-5deg);
}
.pb2{
	transform: translate(0px, 50px)  rotate(5deg);
}





.before1{
	opacity: 0 !important;
	transform: translateY(70px) !important;
}
.before2{
	transform: rotate(-5deg);
}


@media (max-width: 1400px) {
	.picBox{
		width: 400px;
	}
  .pb1, .pb2{
		height: 400px;
		width: 400px;
	}
	.pb1{	
		transform: translate(30px, -100px) rotate(-5deg);
	}
	.pb2{
		transform: translate(0px, 50px)  rotate(5deg);
	}
}
@media (max-width: 1200px) {
  .pb1, .pb2{
		height: 400px;
		width: 400px;
	}
	.pb1{	
		transform: translate(30px, -100px) rotate(-5deg);
	}
	.pb2{
		transform: translate(0px, 50px)  rotate(5deg);
	}
	
	.tb1{
		h1{
			transition: 1s;
			line-height: 1.5;
			font-size: 1em;
			font-weight: 700;
			span{
				font-size: 3.5em;
				font-weight: 800;
			}
		}
	}
	.st{
		//font-size: 1.1em;
		width: 300px;
		line-height: 1.5;
		transition: 1.5s;
	}
}

@media (max-width: 900px) {
	
	.title{
		flex-direction: column;
		//justify-content: space-around;
		align-items: center;
		height: 92vh;
	}
	.picBox{
		width: 800px;
	}
  .pb2 {
    width: 800px;
    height: 600px;
    transform: translate(0px, 150px) rotate(5deg);
	}
	
	.tb1{
		height: 400px;
		text-align: center;
		transform: translateY(80px);
	}
	.st{
		//font-size: 1.1em;
		width: 80vw;
	}
}
@media (max-width: 500px) {
	.st{
		//width: 350px;
	}
}