.intro{
	//height: 100vh;
	background: #FFF3DE;
}
.introBox{
	padding: 13vh 0 0 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	.introTitle{
		font-size: 2.5em;
		//color: #ED2647;
		font-weight: 800;
		transition: 1s;
	}
	.iBox{
		margin-top: 50px;
		text-align: center;
		width: 50%;
		line-height: 1.2;
		transition: 1s;
		span{
			color: #ED2647;
			font-weight: 800;
		}
	}
}

.serviceBox{
	margin-top: 10vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	transition: 1s;
	.sBox{
		margin-bottom: 15vh;
		display: flex;
		justify-content: space-around;
		width: 80vw;
		.service{
			display: flex;
			//flex-direction: column;
			align-items: center;
			img{
				width: 150px;
				margin-right: 10px;
			}
			>div{
				border-left: solid #ED2647;
				height: 150px;
				padding-left: 10px;
				display: flex;
				flex-direction: column;
				width: 150px;
				.sName{
					font-weight: 800;
					font-size: 1.1em;
					margin-bottom: 15px;
				}
			}
		}
	}
}
//  FFFAE1



@media (max-width: 1200px) {
	.serviceBox .sBox .service img {
		width: 100px;
		margin-right: 10px;
	}
	.serviceBox .sBox .service > div {
		height: 100px;
		width: 120px;
	}

}

@media (max-width: 900px) {
	
	.introBox .iBox {
    width: 80%;
	}
	
	.serviceBox{
		flex-direction: row;
		width: 90vw;
		margin-top: 0;
	}
	.serviceBox .sBox {
    margin-bottom: 5vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80vw;
    height: 800px;
		width: inherit;
	}
	.serviceBox .sBox .service img {
		width: 100px;
		margin-right: 10px;
	}
	.serviceBox .sBox .service > div {
		height: 100px;
		width: 100px;
	}
}
@media(max-width: 550px){
	.introTitle{
		font-size: 1.8em !important;
	}
}

@media (max-width: 500px) {
	
	
	.introBox{
		padding: 13vh 0 5vh 0;
	}
	.introBox .iBox {
    margin-bottom: 30px;
	}
	
	.serviceBox{
		flex-direction: column;
		width: 90vw;
		margin-top: 0;
	}
	.serviceBox .sBox {
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80vw;
    height: 450px;
		width: inherit;
		.service{
			//margin-top: 30px;
		}
	}
}
