.nav{
	position: fixed;
	height: 8vh;
	width: 100%;
	background: rgba(255, 255, 255, 0.9);
	border-bottom: solid #D5D5D5 1px;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 3;
}
.navBox{
	width: 80%;
	display: flex;
	align-items: center;
	justify-content: space-around;
	>div:first-child{
		display: flex;
		align-items: center;
		width: 80%;
		>div:first-child{
			display: flex;
			align-items: center;
			.navImg{
				cursor: pointer;
				height: 4vh;
			}
		}
	}
	.menuItem{
		cursor: pointer;
		margin-left: 5%;
		font-size: 1.1em;
		font-weight: 800;
	}
	
	.btnBox{
		display: flex;
		justify-content: right;
		width: 150px;
		button{
			cursor: pointer;
			margin-left: 5%;
			width: 150px;
			height: 5vh;
			font-size: 1em;
			font-weight: 700;
			border: solid #ED2647;
			border-radius: 10px;
			background-color: #ED2647;
			color: white;
			transition: 0.5s;
		}
		button:hover{
			transition: 0.5s;
			background-color: white;
			color: #ED2647;
		}
	}
}


@media (max-width: 850px) {
	.menuItem{
		display: none;
	}
}
@media (max-width: 600px) {
	.navImg{
		height: 3vh !important;
	}
	.btnBox button{
		width: 100px !important;
		height: 3.5vh !important;
	}
}