.tech{
	background: #FEF6ED;
}
.techBox{
	padding: 13vh 0 13vh 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	.techTitle{
		font-size: 2.5em;
		//color: #ED2647;
		font-weight: 800;
		transition: 1s;
	}
	.tBox{
		margin-top: 50px;
		width: 50%;
		margin-bottom: 6vh;
		text-align: center;
		line-height: 1.2;
		transition: 1s;
	}
	.techBox2{
		display: flex;
		width: 90vw;
		justify-content: space-around;
		transition: 1s;
		
		.tech{
			width: 350px;
			height: 600px;
			display: flex;
			flex-direction: column;
			.tName{
				display: flex;
				justify-content: center;
				font-size: 1.3em;
				font-weight: 800;
				padding-bottom: 30px;
				border-bottom: #ED2647;
			}
			.tImage{
				width: 350px;
				height: 600px;
				background-size: cover;
				border-radius: 15px;
				box-shadow: 0 0 20px #BF0037;
			}
			.tim1{
				background-image: url("../img/tech/gpsApp.png");
			}
			.tim2{
				background-image: url("../img/tech/aiApp.png");
			}
			.tim3{
				background-image: url("../img/tech/reviewApp.png");
			}
		}
	}
}

@media(max-width: 1200px){
	.techBox .techBox2 .tech{
		width: 250px;
    height: 500px;
		.tImage{
			width: 250px;
    	height: 500px;
		}
	}
}

@media (max-width: 900px) {
	.techBox .techBox2 .tech{
		width: 200px;
    height: 450px;
		.tName{
			font-size: 1em;
		}
		.tImage{
			width: 200px;
    	height: 450px;
		}
	}
	.tBox {
    width: 80% !important;
	}
}

@media (max-width: 700px) {
	.techBox .techBox2 {
		flex-direction: column;
		align-items: center;
	}
	.techBox .techBox2 .tech{
		width: 350px;
    height: 350px;
		margin-bottom: 30px;
		.tName{
			font-size: 1em;
		}
		.tImage{
			width: 350px;
    	height: 350px;
		}
	}
	.tBox {
    width: 80% !important;
	}
}

@media (max-width: 500px) {
	.techTitle{
		font-size: 1.8em !important;
	}
	.techBox{
		padding: 13vh 0 5vh 0;
	}
	.techBox .techBox2 .tech{
		width: 250px;
    height: 250px;
		.tImage{
			width: 250px;
    	height: 250px;
		}
	}
}